import React, { Component } from 'react';
import './Login.css';
import { connect } from 'react-redux';
import { login } from '../../../action';

class LoginComponent extends Component {
  render() {
    const { login, goBack, goRegister } = this.props;
    return (
      <div className="login-root">
        <form>
          <fieldset>
            <legend></legend>
            <input ref={(username) => {this.usernameEl = username}} type="text" placeholder="" tabIndex="1" />
          </fieldset>
          <fieldset>
            <legend></legend>
            <input ref={(password) => {this.passwordEl = password}} type="password" placeholder="" tabIndex="2" />
          </fieldset>
          <fieldset>
            <span onClick={(e) => goRegister()}></span>
          </fieldset>
          <fieldset>
            <input type="button" value="" onClick={(e) => login(this.usernameEl.value, this.passwordEl.value)} />
            <input type="button" value="" onClick={(e) => goBack()} />
          </fieldset>
        </form>
      </div>
    );
  }
}

export default connect(
  null,
  (dispatch) => ({
    login: (username, password) => {
      dispatch(login(username, password));
    },
    goBack: () => {
      dispatch({
        type: 'Change_Left_Mode',
        mode: 'unlogined'
      });
    },
    goRegister: () => {
      dispatch({
        type: 'Change_Left_Mode',
        mode: 'register'
      });
    }
  })
)(LoginComponent);



