import React, { Component } from 'react';
import './Register.css';
import { connect } from 'react-redux';
import { getAPI } from '../../../http';
import { register } from '../../../action';
import md5 from 'md5';

class RegisterComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      token: md5(new Date().toISOString())
    };
  }

  refreshToken() {
    this.setState({
      token: md5(new Date().toISOString())
    });
  }

  render() {
    const { goBack } = this.props;
    const { token } = this.state;
    return (
      <div className="register-root">
        <form>
          <fieldset>
            <legend></legend>
            <input ref={(username) => { this.usernameEl = username }} type="text" placeholder="" tabIndex="1" />
          </fieldset>
          <fieldset>
            <legend></legend>
            <input ref={(password) => { this.passwordEl = password }} type="password" placeholder="" tabIndex="2" />
          </fieldset>
          <fieldset>
            <legend></legend>
            <input ref={(passwordConfirmed) => { this.passwordConfirmedEl = passwordConfirmed }} type="password" placeholder="" tabIndex="3" />
          </fieldset>
          <fieldset>
            <legend></legend>
            <input ref={(imageCaptcha) => { this.imageCaptchaEl = imageCaptcha }} className="short" type="text" placeholder="" tabIndex="4" />
            <span className="short-info" style={{
              backgroundImage: getAPI("CaptchaImage")(200, 50, token),
              backgroundSize: 'cover'
            }} onClick={(e) => this.refreshToken()}></span>
          </fieldset>
          <fieldset>
            <input type="button" value="" onClick={(e) => this.toRegister(this.usernameEl.value, this.passwordEl.value, this.passwordConfirmedEl.value, this.imageCaptchaEl.value, token)} />
            <input type="button" value="" onClick={(e) => goBack()} />
          </fieldset>
        </form>
      </div>
    );
  }

  toRegister(username, password, passwordConfirmed, imageCaptcha, captchaToken) {
    if (password !== passwordConfirmed) {
      alert('!');
      return;
    }

    username = username || '';
    if (username.length < 6 || username.length > 24) {
      alert('!');
      return;
    }

    password = password || '';
    if (password.length < 6 || password.length > 24) {
      alert('!');
      return;
    }

    const { register } = this.props;
    register(username, password, imageCaptcha, captchaToken);
  }

}

export default connect(
  null,
  (dispatch) => ({
    register: (username, password, imageCaptcha, captchaToken) => {
      dispatch(register(username, password, imageCaptcha, captchaToken));
    },
    goBack: () => {
      dispatch({
        type: 'Change_Left_Mode',
        mode: 'unlogined'
      });
    }
  })
)(RegisterComponent);


